<template>
  <div class="wrapper">
    <form @submit.prevent="post" method="post" action="#">
      <div class="form-group">
        <input type="text" id="fName" name="name" placeholder="Nimi: *">
        <input type="email" id="fEmail" name="email" placeholder="E-mail *">
      </div>
      <div class="form-group">
        <textarea name="feedback"  id="fMsg" placeholder="Tagasiside"></textarea>
        <div class="flex-row">
            <input type="checkbox" id="gdpr" name="gdpr">
            <label for="gdpr"><span>Nõustun, et antud veebileht töötleb mu isikuandmeid ükskõik millisel moel.</span></label>
        </div>
        <input type="submit" class="btn" value="Saada">
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: "feedbackForm",
  data () {
    return {
      submitted: false
    }
  },
  methods: {
    post: function() {
    let formN = document.querySelector('#fName').value;
    let formE = document.querySelector('#fEmail').value;
    let formMsg = document.querySelector('#fMsg').value;

    let data = {email: formE, message: formMsg, name: formN}

      let request = fetch('https://studyweb-75532.firebaseio.com/submissions.json', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: JSON.stringify(data)
      })

      document.getElementById('fName').value = '';
      document.getElementById('fEmail').value = '';
      document.getElementById('fMsg').value = '';
      document.getElementById('gdpr').checked = false;

    }
  }
};
</script>

